<template>
  <nav aria-label="Page navigation example">
    <ul class="pagination justify-content-center">
      <li class="page-item" :class="{ 'disabled': pages.has_pre === false}">
        <a class="page-link" href="#" aria-label="Previous"  @click.prevent="updatePage(pages.current_page-1)" >
          <span aria-hidden="true">&laquo;</span>
        </a>
      </li>
      <li class="page-item" :class="{'active': page ===  pages.current_page}" v-for="page in pages.total_pages" :key="page">
        <a class="page-link"  href="#" @click.prevent="updatePage(page)">{{page}}</a>
      </li>
      <li class="page-item" :class="{ 'disabled': pages.has_next === false}">
        <a class="page-link" href="#" aria-label="Next" @click.prevent="updatePage(pages.current_page+1)">
          <span aria-hidden="true">&raquo;</span>
        </a>
      </li>
    </ul>
  </nav>
</template>

<script>
// :pages="{ 頁碼資訊 }"
// @emitPages="更新頁面事件"
export default {
  props: ['pages'],
  methods: {
    updatePage (page) {
      this.$emit('emit-pages', {
        id: this.event.id,
        title: this.title,
        start: this.start,
        end: this.end
      })
    }
  }
}
</script>
